export class Student {
     name:string;
     english:string;
     math:string;
     hindi:string;
    
constructor(name:string,english:string,math:string,hindi:string){      
        this.name = name;
        this.english = english;
        this.math = math; 
        this.hindi = hindi; 
    }
}



